* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: "Commissioner";
    color: #444;
    background: #ece9e6;
    /* fallback for old browsers */
    background: -webkit-linear-gradient( to right, #ffffff, #ece9e6);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient( to right, #ffffff, #ece9e6);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.container {
    width: 100%;
    padding: 1rem;
    margin: 0 auto;
}

.text-center {
    text-align: center;
}

a {
    text-decoration: none;
    color: #335dfd;
}

.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 -1rem;
}

.row+.row {
    margin-top: 2rem;
}

[class*="col-"] {
    padding: 0 1rem;
    margin-top: 2rem;
}

.d-flex {
    display: flex;
}

.align-items-center {
    align-items: center;
}

.justify-content-center {
    justify-content: center;
}

footer {
    text-align: center;
    margin-top: 4rem;
}

.red {
    color: red;
}

.col-1 {
    flex: none;
    width: calc(100% / 12 * 1);
}

.col-2 {
    flex: none;
    width: calc(100% / 12 * 2);
}

